.bg {
    width: 100%;
    min-height: 420px;

    background-image: url('https://i.ytimg.com/vi/JZlkHkCp75g/maxresdefault.jpg?sqp=-oaymwEmCIAKENAF8quKqQMa8AEB-AH-CYAC0AWKAgwIABABGEsgMCh_MA8=&rs=AOn4CLDagO_cAlzdIkuYNwDZrMw9oyG7IA') ;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right 35% bottom 80%;

    filter: brightness(90%);
    transition: .3s;
}
.bg:hover{
    filter: brightness(100%);
}

